/**
  Variáveis que irão auxiliar na produtividade ao substituir (valores/tamanho/cores) no Quadro de informações.
*/

:root {
	--tamanho-texto-sim-and-nao: 50px;
}

/**
  Responsável em manter a responsividade na aplicação.
*/

.bloco_alta:nth-child(2n) {
	background-color: #e2e2e2;
}

/* Responsável pela fonte no bloco de ( Hora de Entrada ) */

.tempo_decorrido_rpa {
	color: #4d6bf4;
	font-weight: bold;
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: rgb(255, 255, 255);
	font-size: var(--tamanho-texto-sim-and-nao);
}

.tempo_entrada_rpa {
	color: #4d4f53;
	font-size: 2rem;
}

/* Responsável pelo SIM ou NÃO no bloco de biópsia e alergia */
.texto_stilo_sim {
	color: #fe5b3a;
	font-weight: bold;
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: rgb(255, 255, 255);
	font-size: var(--tamanho-texto-sim-and-nao);
}

/* Responsável pelo SIM ou NÃO no bloco de biópsia e alergia */
.texto_stilo_nao {
	color: #4d4f53;
	font-weight: bold;
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: rgb(255, 255, 255);
