.radio-inducao {
	-webkit-appearance: none;
	width: 45px !important;
	height: 45px !important;
	border-radius: 50%;
	outline: none;
}

.radio-inducao:before {
	content: '';
	display: block;
	width: 60% !important;
	height: 60% !important;
	margin: 20% auto;
	border-radius: 50%;
}

.radio-inducao:checked:before {
	background: rgb(60, 60, 60);
}

