
header {
    position: fixed;
    top:0%;left:0px;
    position: relative;
    width:100%;
    height:80px;
    background-color:#E9F7F6 !important;
    box-shadow: 0px 4px 12px rgba(145, 145, 145, 0.25);
}

header p {
    font-size: 23px;
    position: absolute;
    left:2%;top:30%;
}

.options {
    float:right;
    position: relative;
    left:-20px;top:34px;
}